// import  from ""
import empty from "@/components/mycom/empty";
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					dataList: [],
					status: "loading",
					load: false
				},
				{
					state: 1,
					text: '待审核',
					dataList: [],
					status: "loading",
					load: false
				},
				{
					state: 2,
					text: '成功',
					dataList: [],
					status: "loading",
					load: false
				},
				{
					state: 3,
					text: '失败',
					dataList: [],
					status: "loading",
					load: false
				}
			]
		}
	},
	components: {
		empty,
		uniLoadMore
	},
	onLoad() {
		this.loadData()
	},
	onShow() {},
	onHide() {},
	methods: {
		changeTab(e) {
			this.tabCurrentIndex = e.detail.current;
			this.initData()
		},
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		loadData() {
			let num = this.tabCurrentIndex;
			console.log("加载")
			if (this.navList[num].load) {
				return;
			} else {
				let parmas = {
					uid: getApp().globalData.uid,
					type: num,
					nowPage: this.page
				}
				this._reqw.ipost(parmas, "withdrawList").then(res => {
					this.navList[num].status="more"
					res.result == 0 ? (this.totalPage = res.totalPage,res.dataList.length==0?this.navList[num].load=true: res.dataList
						.forEach(item => {
							this.navList[num].dataList.push(item)
						})) : this.$api.tip(res.resultNote)
				}).catch(err => {})
			}

		},
		loadDataMore() {
			let num = this.tabCurrentIndex;
			this.totalPage > this.page ? (this.page += 1, this.loadData()) : setTimeout(() => {
				this.navList[num].status = "noMore"
			}, 30)
		},
		initData() {
			let index = this.tabCurrentIndex;
			this.page = 1;
			this.totalPage = 1;
			this.navList[index].dataList = [];
			// this.navList[index].status = "loading";
			this.navList[index].load = false;
			this.loadData()
		}
	}
}
